html{
  background-color: black;
  height: inherit;
}

.body{
  height: 100%;
}

.App {
  height: 100%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.timer {
  color: red;
  font-size: 30em;
}

.timer-num { 
  color: red;
  font-size: 35em;
  font-family: 'Segment7Standard'; 
  font-weight: normal; 
  font-style: normal; 
}

.timer-container {
  margin-top: 18px;
  text-align: center;
}

.botoes {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 24px;
}

.botao {
  margin: 36px;
  margin-top: 0px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (max-width: 1360px) {
  .timer {
    font-size: 25em;
  }
  
  .timer-num { 
    font-size: 30em;
  }
}

@media only screen and (max-width: 1280px) {
  .timer {
    font-size: 20em;
  }
  
  .timer-num { 
    font-size: 25em;
  }
}

@media only screen and (max-width: 1028px) {
  .timer {
    font-size: 15em;
  }
  
  .timer-num { 
    font-size: 20em;
  }
}

@media only screen and (max-width: 820px) {
  .timer {
    font-size: 10em;
  }
  
  .timer-num { 
    font-size: 15em;
  }

  .botoes {
    margin-top: 18px;
  }

  .botoes {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}

@media only screen and (max-width: 610px) {
  .timer {
    font-size: 5em;
  }
  
  .timer-num { 
    font-size: 5em;
  }
}


